module.exports = "import React from \"react\";\nimport { EventChannel } from \"suplink-sdk\";\n\nexport default class A extends React.Component {\n  componentDidMount() {\n    EventChannel.on(\"eventName\", this.EventFunc);\n  }\n\n  EventFunc = () => {\n    alert(\"你点击了\");\n  };\n\n  componentWillUnmount() {\n    //  组件销毁别忘记取消事件\n    console.log(\"unmount\");\n    EventChannel.off(\"eventName\", this.EventFunc);\n  }\n\n  render() {\n    return (\n      <button\n        onClick={() => {\n          EventChannel.emit(\"eventName\");\n        }}\n      >\n        点击\n      </button>\n    );\n  }\n}\n"