module.exports = "import React from \"react\";\nimport { keyboardScrollIntoView } from \"suplink-sdk\";\n\n//  控制输入框在移动设备上防止被键盘遮挡\n\nclass Demo extends React.Component {\n  render() {\n    return (\n      <div style={{ height: 667, width: \"100%\", position: \"relative\", overflow: \"overlay\" }}>\n        <div style={{ height: 110, background: \"#775698\" }}></div>\n        <div style={{ height: 110, background: \"#775698\" }}></div>\n        <div style={{ height: 110, background: \"#775698\" }}></div>\n        <div style={{ height: 110, background: \"#775698\" }}></div>\n        <div style={{ height: 110, background: \"#775698\" }}></div>\n        <input\n          style={{\n            marginTop: 100,\n          }}\n        ></input>\n      </div>\n    );\n  }\n}\n\nexport default keyboardScrollIntoView()(Demo);\n"