import Bridge from "suplink-sdk/bridge";
import { Return, Params } from "./interface";

export default function upload(params: Params = {}): Promise<Return> {
  const { type = 2, max = 9, mediaType = 0 } = params;
  return Bridge.dispatchAppEvent("sl.multiMedia.fileSelector", {
    ...params,
    optionalType: type,
    maximumCount: max,
    mediaContentType: mediaType
  });
}
