import { dispatchAppEvent } from "suplink-sdk/bridge";
import { DatePickerParams, DatePickerReturn } from "./interface";
import React from "react";
import moment from "moment";

const TimeIcon = `data:image/png;base64,`;
const TimeIconDisable = `data:image/png;base64,`;
interface DatePickerProps {
  style?: React.CSSProperties;
  width?: string | number;
  className?: string;
  iconStyle?: React.CSSProperties;
  disabled?: boolean;
  locale?: "zh" | "en";
  icon?: any;
  value?: Date;
  iconPosition?: "start" | "end";
  placeholder?: string;

  onChange?: (date: Date) => any;
  format?: "YYYY-MM-DD" | "YYYY-MM-DD hh:mm" | "YYYY-MM-DD hh:mm:ss" | "hh:mm";
  startDate?: string | number | Date | undefined;
  enzzate?: string | number | Date | undefined;
}

interface DatePickerState {
  value: Date | undefined;
}
export default class DatePicker extends React.PureComponent<DatePickerProps, DatePickerState> {
  static getDerivedStateFromProps(props: DatePickerProps, state: DatePickerState) {
    if (props.value !== state.value && props.value) {
      return {
        value: props.value,
      };
    }
    return null;
  }
  constructor(props: DatePickerProps) {
    super(props);
    this.state = {
      value: undefined,
    };
  }

  __setTime = ({ title = "", currentDate = new Date().getTime(), startDate, enzzate }: DatePickerParams) => {
    if (this.props.disabled) return;
    const format = this.props.format || "YYYY-MM-DD";
    startDate = startDate ? moment(startDate).format(format) : undefined;
    enzzate = enzzate ? moment(enzzate).format(format) : undefined;
    if (currentDate) {
      currentDate = new Date(currentDate).getTime();
    }

    const __format: {
      "YYYY-MM-DD": string;
      "YYYY-MM-DD hh:mm": string;
      "YYYY-MM-DD hh:mm:ss": string;
      "hh:mm": string;
    } = {
      "YYYY-MM-DD": "yyyy-MM-dd",
      "YYYY-MM-DD hh:mm": "yyyy-MM-dd HH:mm:ss",
      "YYYY-MM-DD hh:mm:ss": "yyyy-MM-dd HH:mm",
      "hh:mm": "HH:mm",
    };
    dispatchAppEvent("reqDatePicker", {
      format: __format[format] || "yyyy-MM-dd",
      currentDate: currentDate ? currentDate : new Date().getTime(),
      title,
    }).then((res: DatePickerReturn) => {
      if (typeof this.props.onChange === "function") {
        this.props.onChange(new Date(Number(res.data)));
      } else {
        this.__onChange(new Date(Number(res.data)));
      }
    });
  };
  __onChange = (value: Date) => {
    this.setState({
      value,
    });
  };
  render() {
    const { width = 270, style = {}, className = "", iconStyle = {}, disabled = false, locale = "zh", icon, iconPosition = "end", format = "YYYY-MM-DD" } = this.props;
    let placeholder = "";
    if (locale === "zh") {
      placeholder = this.props.placeholder ? this.props.placeholder : "请设置时间";
    } else {
      placeholder = this.props.placeholder ? this.props.placeholder : "setting time";
    }

    return (
      <div
        className={className}
        onClick={() =>
          this.__setTime({
            startDate: this.props.startDate,
            enzzate: this.props.enzzate,
          })
        }
        style={{
          height: 26,
          background: "#F5F5F5",
          minWidth: 126,
          position: "relative",
          borderRadius: 13,
          width,
          cursor: "pointer",
          paddingLeft: 15,
          display: "flex",
          alignItems: "center",
          ...style,
        }}
      >
        <span style={{ color: disabled ? "rgba(0, 0, 0, 0.38)" : "#3F3F3F" }}>
          {this.state.value ? moment(this.state.value).format(format) : <span style={{ color: "#E1E1E1" }}>{placeholder}</span>}
        </span>

        <div
          style={{
            height: 20,
            width: 20,
            position: "absolute",
            top: "50%",
            display: "flex",
            transform: "translateY(-50%)",
            left: iconPosition === "start" ? 8 : "initial",
            right: iconPosition === "end" ? 8 : "initial",
            ...iconStyle,
          }}
        >
          {icon ? icon : <img height="100%" width="100%" src={disabled ? TimeIconDisable : TimeIcon} alt={locale === "zh" ? "选择时间" : "select time"} />}
        </div>
      </div>
    );
  }
}
