module.exports = "import React from \"react\";\nimport { ConnectedRouter } from \"connected-react-router\";\nimport { createHashHistory, createBrowserHistory, History, Location, LocationDescriptorObject, LocationState } from \"history\";\nimport {\n  Switch,\n  Route,\n  Redirect,\n  // HashRouter as Router,\n} from \"react-router-dom\";\n\nimport { ConnectNavbar } from \"suplink-sdk\";\n\nenum TypeMode {\n  history = \"history\",\n  hash = \"hash\",\n}\n\ntype RouterConfig = {\n  redirect: string;\n  mode: TypeMode;\n};\n\nconst basename = \"\";\n\nconst routerConfig: RouterConfig = {\n  redirect: \"/\",\n  mode: TypeMode.history, // 配置hashRouter还是historyRouter\n};\n\ntype Routes = {\n  path: string;\n  title: string;\n  component?: any;\n  meta?: {\n    exact?: boolean;\n  };\n};\n\nfunction Component1() {\n  return \"组件1\";\n}\nfunction Component2() {\n  return \"组件2\";\n}\nconst routes: Routes[] = [\n  {\n    path: \"/\",\n    component: Component1,\n    title: \"首页\",\n  },\n\n  {\n    path: \"/select\",\n    component: Component2,\n    title: \"其他页\",\n  },\n];\n\nlet history: History<Location<LocationDescriptorObject<LocationState>>>;\nif (routerConfig.mode === TypeMode.hash) {\n  history = createHashHistory({\n    basename: basename,\n  });\n} else {\n  history = createBrowserHistory({\n    basename: basename,\n  });\n}\n\nexport default class AppRoutes extends React.Component {\n  render() {\n    return (\n      <ConnectNavbar routes={routes} history={history} homePath=\"/\">\n        <ConnectedRouter history={history}>\n          <Switch>\n            {routes.map((route) => {\n              const { path, component, meta = {}, title } = route;\n\n              return <Route path={path} key={title} exact={meta.exact ? true : false} component={component} />;\n            })}\n            <Redirect to=\"/\" />\n          </Switch>\n        </ConnectedRouter>\n      </ConnectNavbar>\n    );\n  }\n}\n"