import React from "react";
import { History, Location, LocationDescriptorObject, LocationState } from "history";
import { parse } from "../../utils";
import { navConfig } from "suplink-sdk";
type Route = {
  path: string; // 路由的路径
  title: string; // 标题

  [key: string]: any;
};

type TypeRouteObj = {
  routes: Route[];
  history: History<Location<LocationDescriptorObject<LocationState>>>;
  homePath?: string;
};

class ConnectNavbar extends React.Component<TypeRouteObj> {
  historyLength: number;
  constructor(props: TypeRouteObj) {
    super(props);
    //    重写 路由方法
    const { history } = this.props;
    const replace = history.replace;
    const push = history.push;
    this.historyLength = 1;
    const __push = this.push.bind(this);
    const __replace = this.replace.bind(this);
    const __popstate = this.popstate.bind(this);
    history.push = function (path: string, state?: Location<LocationDescriptorObject<unknown>> | undefined) {
      __push();
      return push(path, state);
    };
    history.replace = function (path: string, state?: Location<LocationDescriptorObject<unknown>> | undefined) {
      __replace();
      return replace(path, state);
    };

    window.addEventListener("popstate", __popstate);
  }

  push() {
    Promise.resolve().then(() => {
      this.setTitle();
    });
  }

  replace() {
    Promise.resolve().then(() => {
      this.setTitle();
    });
  }
  popstate() {
    let timer = setTimeout(() => {
      this.setTitle();
      clearTimeout(timer);
    });
  }

  setTitle = () => {
    let { homePath = "/" } = this.props;
    let path = this.props.history.location.pathname;

    let title = parse(this.props.history.location.search).title;
    let currentRoute = this.props.routes.find((item) => item.path === path) || {
      title: "",
    };
    navConfig({
      title: (title || currentRoute.title || "") as string,
      theme: "dark",
      backBtn: path === homePath ? false : true,
    });
  };
  componentDidMount() {
    this.setTitle();
  }

  render() {
    return this.props.children;
  }
}

export default ConnectNavbar;
